.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.NavLink {
  font-size: 24px;
  font-weight: 700;
  text-decoration: none;
  color: tomato;
}
.Link {
  display: flex;
  margin-left: 10px;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.Section {
  padding: 20px;
}
.Title {
  font-weight: 900;
  font-size: 32px;
  color: tomato;
}
.Content {
  display: flex;
  justify-content: center;
  text-align: center;
}
